/* Контейнер для основного наполнения страницы */

.section__container {
    margin: 0;
    display: grid;
    grid-template-rows: auto auto 1fr;
    height: 100vh;
    align-items: end;
  }
  
  /* Адаптив под экраны */
  
  @media screen and (max-width: 1023px) {
    .section__container {
      width: calc(668/768*100%);
    }
  }
  
  @media screen and (max-width: 767px) {
    .section__container {
      width: calc(284/320*100%);
    }
  }